import * as React from "react";
import {StatelessComponent} from "react";
import {uniq} from "underscore";

export const InputField: StatelessComponent<{
    tag: string,
    i: number,
    onChangeListener: (k: string, v: string) => any
}> = ({tag, i, onChangeListener}) => {
    return <div
        className="tr template-tag mdc-form-field">
        <label className={"td"} htmlFor={`tag-${i}`}>{tag}</label>
        <div className="td mdc-text-field">
            <input onChange={(e) => onChangeListener(tag, e.target.value)}
                   type="text"
                   className="mdc-text-field__input"
                   id={`tag-${i}`} placeholder={tag}/>
            <div className="mdc-text-field__bottom-line"/>
        </div>
    </div>;
};
export const InputFields: StatelessComponent<{
    tags?: any,
    onChangeListener: (k: string, v: string) => any
}> = (props) =>
    <div
        className="table"
        id="template-fields">
        {!!props.tags ? uniq(Object.keys(props.tags)).map((tag, i: number) =>
            <InputField key={i} onChangeListener={props.onChangeListener}
                        tag={tag as string} i={i}/>) : ""}
    </div>;



